  height: 150px;
  animation: 3s linear spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loading {
  justify-content: center;
  align-items: center;
  display: flex;
}

