}

.animation {
  animation: 1s ease-in-out aparecer;
}

.number2, .number1 {
  opacity: 0;
  position: absolute;
}

@keyframes aparecer {
  from {
    opacity: 0;
    transform: scale(.8);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

.aviso {
  opacity: 0;
